.Layout {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.LoginText {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.LoginTitle {
  font-size: 18px;
  font-weight: 600;
}

.LoginHello {
  font-size: 14px;
  font-weight: 400;
}

.LoginFrame {
  margin-bottom: 130px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  width: 100%;
  max-width: 400px;
  gap: 20px;
}

.InputContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.ImageFrame {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ButtonContainer {
  display: flex;
  flex-direction: column;
}
